  .app-bar .app-bar__navigation.open {\r\n    left: 0;\r\n  }\r\n   \r\n  .app-bar .app-bar__navigation ul li a {\r\n    display: inline-block;\r\n    text-decoration: none;\r\n    color: black;\r\n    padding: 8px;\r\n    margin-bottom: 5px;\r\n    width: 100%;\r\n    min-width: 44px;\r\n    min-height: 44px;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li a:hover {\r\n    background-color: #952323;\r\n    color: white;\r\n  }\r\n   \r\n  /*\r\n    Main Content\r\n  */\r\n   \r\n  main {\r\n    padding: 32px;\r\n    flex: 1;\r\n  }\r\n   \r\n  .content {\r\n    margin: 0 auto;\r\n    min-height: 100%;\r\n    text-align: center;\r\n  }\r\n   \r\n  .content .content__heading {\r\n    font-weight: normal;\r\n    margin-bottom: 16px;\r\n    color: #952323;\r\n  }\r\n\r\n  /* \r\n  Hero\r\n  */\r\n\r\n  .hero {\r\n    /* background-image: url('../public/image/hero-image_2.jpg'); */\r\n    background-size: cover;\r\n    padding: 2em 0;\r\n    margin-bottom: 2em;\r\n    text-align: center;\r\n    align-items: center;\r\n    justify-content: center;\r\n    display: flex;\r\n    min-height: 380px;\r\n    width: 100%;\r\n    position: relative;\r\n    overflow: hidden; /* Menyembunyikan bagian gambar yang mungkin melewati batas .hero */\r\n  }\r\n  \r\n  .picture-wrapper {\r\n    width: 100%; /* Membuat .picture-wrapper penuh lebar */\r\n  }\r\n  \r\n  .picture-wrapper picture,\r\n  .picture-wrapper img {\r\n    width: 100%; /* Membuat gambar penuh lebar */\r\n    height: auto; /* Memastikan aspek gambar terjaga */\r\n  }\r\n  \r\n  .hero__inner {\r\n    width: 100%;\r\n    text-align: center;\r\n    margin-left: auto;\r\n    margin-right: auto;\r\n  }\r\n\r\n  .hero__overlay {\r\n    position: absolute;\r\n    top: 50%; /* Pusatkan vertikal */\r\n    left: 50%; /* Pusatkan horizontal */\r\n    transform: translate(-50%, -50%); /* Pusatkan dengan tepat */\r\n    text-align: center; /* Pusatkan teks */\r\n    color: #fff; /* Warna teks */\r\n  }\r\n  \r\n  .hero__title {\r\n    font-size: 3em;\r\n    font-weight: 100;\r\n    margin-bottom: 0.25em;\r\n    color: #f3f0ee;\r\n    font-weight: bold;\r\n    text-shadow: 2px 2px 2px #A73121;\r\n  }\r\n\r\n  .hero__tagline {\r\n    font-size: 1.5em;\r\n    font-weight: 100;\r\n    margin-bottom: 0.25em;\r\n    color: #A73121;\r\n  }\r\n\r\n  /* \r\n  Restaurant\r\n  */\r\n\r\n  .restos {\r\n    display: grid;\r\n    grid-template-columns: 1fr;\r\n    gap: 16px;\r\n    margin-bottom: 32px 0;\r\n  }\r\n\r\n  /* \r\n  Restaurant Item\r\n  */\r\n\r\n  .resto-item {\r\n    width: 100%;\r\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n    border-radius: 8px;\r\n    overflow: hidden;\r\n    background-color: #f3f0ee;\r\n  }\r\n   \r\n  .resto-item__header {\r\n    position: relative;\r\n  }\r\n   \r\n  .resto-item .resto-item__header .resto-item__header__poster {\r\n    width: 100%;\r\n  }\r\n   \r\n  .resto-item .resto-item__header .resto-item__header__rating {\r\n    position: absolute;\r\n    padding: 8px;\r\n    bottom: 20px;\r\n    left: 0;\r\n    display: inline-block;\r\n    background-color: black;\r\n    color: white;\r\n  }\r\n   \r\n  .resto-item .resto-item__header .resto-item__header__rating .resto-item__header__rating__score {\r\n    margin-left: 10px;\r\n  }\r\n   \r\n  .resto-item .resto-item__content {\r\n    padding: 16px;\r\n  }\r\n   \r\n  .resto-item .resto-item__content h3 {\r\n    margin: 0 0 10px 0;\r\n  }\r\n   \r\n  .resto-item .resto-item__content h3 a {\r\n    color: #A73121;\r\n    text-decoration: none;\r\n    font-size: xx-large;\r\n  }\r\n   \r\n  .resto-item .resto-item__content p {\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 4; /* number of lines to show */\r\n    -webkit-box-orient: vertical;\r\n  }\r\n\r\n  /* \r\n  Restaurant Detail\r\n  */\r\n  \r\n  /* .resto-detail {\r\n    margin: 0 auto;\r\n    width: 100%;\r\n    max-width: 800px;\r\n    display: grid;\r\n    grid-template-columns: 1fr;\r\n    gap: 18px 16px;\r\n  } */\r\n\r\n  .resto__title {\r\n    font-size: 2em;\r\n    font-weight: bold;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n  }\r\n\r\n  .resto__poster {\r\n    width: 100%;\r\n    height: 400px;\r\n    object-fit: cover;\r\n    object-position: center;\r\n    border-radius: 8px;\r\n  }\r\n\r\n  .resto__info {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 8px;\r\n  }\r\n\r\n  .resto__info h3 {\r\n    font-size: 1.5em;\r\n    font-weight: bold;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n  }\r\n\r\n  .resto__info p {\r\n    font-size: 1em;\r\n    font-weight: normal;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n  }\r\n\r\n  .resto__info h4 {\r\n    font-size: 1em;\r\n    font-weight: bold;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n  }\r\n\r\n  /* .resto__info ul {\r\n    list-style: none;\r\n    padding: 0;\r\n    margin: 0;\r\n    display: flex;\r\n    flex-direction: row;\r\n    flex-wrap: wrap;\r\n    gap: 8px;\r\n  } */\r\n\r\n  .resto__info .resto__info__categories {\r\n    list-style: none;\r\n    padding: 0;\r\n    margin: 0;\r\n    display: flex;\r\n    flex-direction: row;\r\n    flex-wrap: wrap;\r\n    gap: 8px;\r\n  }\r\n\r\n  .resto__info .resto__info__category {\r\n    padding: 0.3em 1em;\r\n    color: #ffffff;\r\n    background-color: #A73121;\r\n    margin-right: 2px;\r\n    border: 1px solid transparent;\r\n    border-radius: 0px 10px 0px 10px;\r\n  }\r\n\r\n  .resto__info__title {\r\n    font-size: 1.25em;\r\n    color: #952323;\r\n  }\r\n\r\n  .resto__info__title p {\r\n    font-weight: bold;\r\n  }\r\n\r\n  /* \r\n  Restaurant Menus\r\n  */\r\n\r\n  .resto-menus__heading {\r\n    font-size: 2em;\r\n    font-weight: normal;\r\n    margin-top: 10px;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n    text-align: center;\r\n  }\r\n\r\n  .resto-menus {\r\n    display: grid;\r\n    grid-template-columns: repeat(2, 1fr);\r\n    gap: 30px;\r\n    margin-bottom: 32px 0;\r\n  }\r\n\r\n  .resto-menus__foods {\r\n    text-align: left;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-content: center;\r\n  }\r\n\r\n  .resto-menus__foods h3 {\r\n    font-size: 1.5em;\r\n    font-weight: 200;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n  }\r\n\r\n  .resto-menus__foods li {\r\n    list-style: none;\r\n    padding: 0.3em 1em;\r\n    color: #ffffff;\r\n    background-color: #A73121;\r\n    margin-right: 2px;\r\n    border: 1px solid transparent;\r\n    border-radius: 0px;\r\n    margin-bottom: 5px;\r\n  }\r\n\r\n  .resto-menus__drinks {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-content: center;\r\n  }\r\n\r\n  .resto-menus__drinks h3 {\r\n    font-size: 1.5em;\r\n    font-weight: 200;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n  }\r\n\r\n  .resto-menus__drinks li {\r\n    list-style: none;\r\n    padding: 0.3em 1em;\r\n    color: #ffffff;\r\n    background-color: #A73121;\r\n    margin-right: 2px;\r\n    border: 1px solid transparent;\r\n    border-radius: 0px;\r\n    margin-bottom: 5px;\r\n  }\r\n\r\n  /* \r\n  Restaurant Review\r\n  */\r\n\r\n  .resto-review__heading {\r\n    font-size: 1.5em;\r\n    font-weight: normal;\r\n    margin-top: 10px;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n    text-align: center;\r\n  }\r\n\r\n  .resto-review {\r\n    display: grid;\r\n    grid-template-columns: 1fr;\r\n    gap: 16px;\r\n    margin-bottom: 32px 0;\r\n  }\r\n\r\n  .review-item {\r\n    width: 100%;\r\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n    border-radius: 8px;\r\n    overflow: hidden;\r\n    background-color: #f3f0ee;\r\n  }\r\n\r\n  .review-item__header {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 1em 1em;\r\n  }\r\n\r\n  .review-item .review-item__header .review-item__header__name {\r\n    font-size: 1em;\r\n    font-weight: bold;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n  }\r\n\r\n  .review-item .review-item__header .review-item__header__date {\r\n    font-size: 0.8em;\r\n    font-weight: normal;\r\n    margin-bottom: 0.25em;\r\n    color: #952323;\r\n  }\r\n\r\n  .review-item .review-item__content {\r\n    padding: 16px;\r\n  }\r\n\r\n  .review-item .review-item__content p {\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 4; /* number of lines to show */\r\n    -webkit-box-orient: vertical;\r\n  }\r\n\r\n  /* \r\n    Favorite\r\n  */\r\n  \r\n  .fav {\r\n    width: 55px;\r\n    height: 55px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    background-color: #952323;\r\n    \r\n    position: fixed;\r\n    bottom: 16px;\r\n    right: 16px;\r\n    border-radius: 50%;\r\n    border: 0;\r\n    \r\n    font-size: 18px;\r\n    color: white;\r\n    cursor: pointer;\r\n  }\r\n\r\n  /*\r\n    Footer\r\n  */\r\n   \r\n  footer {\r\n    padding: 16px;\r\n  }\r\n   \r\n  footer #footerTools {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    gap: 8px;\r\n  }\r\n   \r\n  footer p {\r\n    margin-top: 16px;\r\n   \r\n    text-align: center;\r\n    color: #aaaaaa;\r\n  }\r\n   \r\n  footer p a {\r\n    color: #952323;\r\n    text-decoration: none;\r\n  }"],"sourceRoot":""}]);
